import React from "react";
import { ZoneWayTransfer } from "zerod";
import { Icon } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC } = HOC;
import doc from "zerod/components/ZoneWayTransfer/doc.md";
export default AmdDocHOC(doc, {
	demo1: function() {
		class Myjavascript extends React.PureComponent {
			state = {
				leftData: [
					{ name: "风中飘摇" ,children:[{name:"由于天涯"},{name:"泪如雨下"},{name:"阳光舞台",disabled:true},{name:"天下轨道"}]},
					{ name: "枫叶南山" },
					{ name: "相信时代(禁用状态)", disabled: true },
					{ name: "天下三官" ,children:[{name:"由于天涯"},{name:"泪如雨下"}]},
				],
				rightData: [],
			};
			methods = {
				onChange: (actionType, rightData, actionItem, sibligItem) => {
					//actionType="transfer" 从左转移到右
					//actionType="move" 右框内移动排序
					//actionType="remove" 拖出右框移除
					//actionType="selectAll" 全选
					//actionType="clearAll 清空
				},
			};
			render() {
				return (
					<ZoneWayTransfer
						style={{ width: "650px" }}
						repeated={true}
						onChange={this.methods.onChange}
						leftSourceData={this.state.leftData}
						rightTargetData={this.state.rightData}
						leftTitle="左边"
						rightTitle="右边"
						sourceKeys={{ name: "name", id: "id",children:"children" }}
						leftItemRender={(item)=><span>{item.name}</span>}
						rightItemRender={(item)=><span>{item.name}</span>}
					/>
				);
			}
		}
		return <Myjavascript />;
	},
});
